body {
  background-color: #272725;
}

@font-face {
  font-family: "oswald";
  src: url("/assets/Oswald-Regular.ttf");
}

.pageimage {
  width: 100%;
  z-index: 0;
}

.introtext {
  margin: auto;
  width: 80%;
  padding: 10px;
  color: white;
  font-family: aktiv-grotesk, sans-serif;
  font-size: 22px;  
}

.divider {
  margin-top: 50px;
  margin-bottom: 50px;
}

.current {
    color: green;
}

.title {
  margin: auto;
  width: 80%;
  padding: 10px;
  color: white;
  font-family: aktiv-grotesk, sans-serif;
  font-size: 30px;  
}

// Header
.outercontainer {
  margin: auto;
  padding: 5px;
}

.titlecontainer {
  margin: auto;
  float: left;
  width: 40%;
  margin-left: 110px;
}

.navcontainer {
  float: right;
  padding: 10px;
}

.outercontainer:after {
  content: "";
  display: table;
  clear: both;
}

// contact

.contactcontainer {
  margin: auto;
  padding: 5px;
}

.leftcontainer {
  margin: auto;
  float: left;
  width: 40%;
  margin-left: 110px;
}

.rightcontainer {
  float: right;
  width: 40%;
  padding: 10px;
}

.mapcustom {
  margin-top: 100px;
  width: 640px; 
  height: 480px;
}

.contactcontainer:after {
  content: "";
  display: table;
  clear: both;
}

.pagetitle {
  margin: auto;
  width: 80%;
  padding: 10px;
  color: white;
  font-family: aktiv-grotesk, sans-serif;
  font-size: 60px;
  margin-top: 100px;
  margin-bottom: 100px;  
}

.sectiontitle {
  margin: auto;
  width: 80%;
  padding: 10px;
  color: white;
  font-family: aktiv-grotesk, sans-serif;
  font-size: 40px;   
}

.menulink {
  color: white;
  font-family: aktiv-grotesk, sans-serif;
  font-size: 20px;
  margin-right: 20px;
}

.contenttext {
  margin: auto;
  width: 80%;
  padding: 10px;
  // line-height: 2;
  color: white;
  font-family: aktiv-grotesk, sans-serif;
  font-style: normal;
  font-weight: 200;
  font-size: 22px;  
}

.li {
  color: white;
  font-family: aktiv-grotesk, sans-serif;
  font-style: normal;
  font-weight: 200;
  font-size: 16px;
}

.projectlink {
  color: #949189;
  text-decoration: underline;
}

.pageimage {
  
}

.column {
  float: left;
  width: 33.33%;
}

/* Clear floats after the columns */
.triplerow:after {
  content: "";
  display: table;
  clear: both;
}

.projectimage {
  width: 500px;  
}
.footer {
  margin-top: 200px;
  color: white;
  font-family: aktiv-grotesk, sans-serif;
  font-size: 24px; 
}

a:link {
  text-decoration: none;
}